<template>
  <div id="single-blog">
      <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
      <li v-for="category in blog.categories">
          {{category}}
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name:"single-blog",
  data(){
    return {
      id:this.$route.params.id,
      blog:{}
    }
  },
  created() {
    //https://vuedemo-b1233.firebaseio.com/posts.json
    // this.$http.get('http://jsonplaceholder.typicode.com/posts/'+ this.id)
    this.$http.get('https://vuedemo-b1233.firebaseio.com/posts/'+ this.id +'.json')
      .then(function (data) {
         console.log(data)
        return data.json();
        // this.blog = data.body;
      })
      .then(function (data) {
        this.blog = data;
      })
  }
}
</script>
<style>
  #single-blog{
    max-width: 960px;
    margin:0 auto;
    paadding:20px;
    background: #eee;
    border:1px dotted #aaa;
  }
</style>



